
.author__info_description {
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: flex-start;
    text-align: left;
    gap: 20px;
    margin-top: 20px;
}

.author__info_description_item {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    padding-top: 10px;
}
.author__info_description_item:first-child {
    padding-top: 0;
}

.author__info_description_item_title {
    font-size: 24px;
    font-weight: bold;    
    color: #FFFFFF;
    font-family: 'Noto Sans', serif;
}
.author__info_description_item_text {
    margin-top: 10px;
    margin-left: 10px;
    font-size: 18px;
    font-weight: bold;
    color: #A9BEC9;
    font-family: 'Noto Sans', serif;
    white-space: pre-line;
}
.author__info_description_item_username {
    font-size: 22px;
}


@media(max-width: 600px) {
    .author__info_description_item_title {
        font-size: 18px;
    }
    .author__info_description_item_text {
        font-size: 14px;
    }
    .author__info_description_item_username {
        font-size: 18px;
    }    
}

