
.author__info_menu {
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    background-color: #002336;
    padding: 25px 45px;
}

.author__info_menu_item {
    display: block;
    width: 100%;

    border-bottom: 2px solid #0A4E73;
    margin-top: 30px;
}
.author__info_menu_item.active {
    border-bottom: 3px solid #FFFFFF;
}
.author__info_menu_item:first-child {
    margin-top: 0;
}

.author__info_menu_item_title {
    font-size: 32px;
    font-weight: bold;
    color: #FFFFFF;
    text-align: start;
}

@media(max-width: 1000px) {
    .author__info_menu {
        display: none;
    }
}
